*{
    box-sizing: border-box;
}
#sw{
            display: inline-block;
            justify-content: space-between;
            max-width: 350px;
            text-align: left;
            

      img {
        margin:8px;
        width:20px;
        height:20px;
        
      }
      button{
        margin: 8px;
        padding: 0px;
        border-right: none;
        border-left: none;
        /* position: relative; */
        /* top: 2px; */
        width: 20px;
        height: 20px;
        font-size: 8px;
        border-top: 1.5px solid black;
        border-bottom: 1.5px solid black;
        background-color: white;
        display: inline-block;
        font-weight: bold;

        
      }
    }
    .clicked{
        border: 1px solid black;
        padding: 2px;
        min-width: 27px;
        min-height: 27px;
    }